import { createStore } from 'vuex'
import axios from 'axios';

export default createStore({
  state: {
    count: 1,
    api_url: process.env.VUE_APP_API,
    api_token: process.env.VUE_APP_TOKEN,
    leagues: [],
    leagueInfo: null,
    teamInfo: null,
    teamFixtures: null,
    players: null,
    matchInfo: null
  },
  getters: {
  },
  mutations: {
    SET_LEAGUES(state, leagues) {
      state.leagues = leagues
      console.log(leagues)
    },
    SET_LEAGUEINFO(state, value) {
      state.leagueInfo = value
      console.log(value)
    },
    SET_TEAM_INFO(state, value) {
      state.teamInfo = value;
      console.log(value)
    },
    SET_TEAM_FIXTURES(state, value) {
      state.teamFixtures = value;
      // console.log('fixtures:', value)
    },
    SET_TEAM_PLAYERS(state, value) {
      state.players = value
      // console.log('players:', value)
    },
    SET_MATCH_INFO(state, value) {
      state.matchInfo = value
      console.log(value)
    }
  },
  actions: {
    async getLeagueInfo({state, commit}, id) {
      const options = {
        method: 'GET',
        url: `${state.api_url}/v3/standings`,
        params: {
          season: '2022',
          league: id
        },
        headers: {
          'X-RapidAPI-Key': state.api_token,
          'X-RapidAPI-Host': 'api-football-v1.p.rapidapi.com'
        }
      };
      
      try {
        const response = await axios.request(options);
        commit('SET_LEAGUEINFO', response.data.response[0])
      } catch (error) {
        console.error(error);
      }
    },
    async getLeagues({state, commit}, name) {
      const options = {
        method: 'GET',
        url: `${state.api_url}/v3/leagues`,
        params: {type: 'league', country: name ? name : null},
        headers: {
          'X-RapidAPI-Key': state.api_token,
          'X-RapidAPI-Host': 'api-football-v1.p.rapidapi.com'
        }
      };
      
      try {
        const response = await axios.request(options);
        commit('SET_LEAGUES', response.data.response)
      } catch (error) {
        console.error(error);
      }
    },
    async getTeamInfo({state, commit}, obj) {
      const options = {
        method: 'GET',
        url: `${state.api_url}/v3/teams/statistics`,
        params: {
          league: obj.league,
          season:"2022",
          team: obj.team
        },
        headers: {
          'X-RapidAPI-Key': state.api_token,
          'X-RapidAPI-Host': 'api-football-v1.p.rapidapi.com'
        }
      };
      
      try {
        const response = await axios.request(options);
        commit('SET_TEAM_INFO', response.data.response)
      } catch (error) {
        console.error(error);
      }
    },
    async getFixturesByTeamId({state, commit}, id) {
      const options = {
        method: 'GET',
        url: `${state.api_url}/v3/fixtures`,
        params: {
          season:"2022",
          team: id
        },
        headers: {
          'X-RapidAPI-Key': state.api_token,
          'X-RapidAPI-Host': 'api-football-v1.p.rapidapi.com'
        }
      };
      
      try {
        const response = await axios.request(options);
        // console.log('teamFIXTURES_INFO:::::', response.data);
        commit('SET_TEAM_FIXTURES', response.data.response)
      } catch (error) {
        console.error(error);
      }
    },
    async getPlayersInfo({state, commit}, obj) {
      const options = {
        method: 'GET',
        url: `${state.api_url}/v3/players`,
        params: {
          season:"2022",
          team: obj.id,
          search: obj.search ? obj.search : null 
        },
        headers: {
          'X-RapidAPI-Key': state.api_token,
          'X-RapidAPI-Host': 'api-football-v1.p.rapidapi.com'
        }
      };
      
      try {
        const response = await axios.request(options);
        // console.log('playersInfo:::::', response.data);
        commit('SET_TEAM_PLAYERS', response.data.response)
      } catch (error) {
        console.error(error);
      }
    },
    async getMatchInfo ({state, commit}, id) {
      const options = {
        method: 'GET',
        url: `${state.api_url}/v3/fixtures`,
        params: {
          id: id
        },
        headers: {
          'X-RapidAPI-Key': state.api_token,
          'X-RapidAPI-Host': 'api-football-v1.p.rapidapi.com'
        }
      };
      
      try {
        const response = await axios.request(options);
        commit('SET_MATCH_INFO', response.data.response)
      } catch (error) {
        console.error(error);
      }
    }
    
    
  },
  modules: {
  }
})
