<template>
  <div class="home container">
    <h1>All leagues</h1>
    <input type="text" placeholder="Enter the country name" v-model="country" @keyup="changeValue" @keydown="deleteTimer">
    <ul class="home--leagues" v-if="leagues.length > 0">
      <li class="home--leagues-league" v-for="(item, index) in leagues" :key="index" @click="$router.push({path: `/league/${item.league.id}`})">
        <img :src="item.league.logo" :alt="item.league.name">
        <span>
          {{item.league.name}}
        </span>
      </li>
    </ul>
    <div v-else>No leagues found, sorry</div>
  </div>
</template>

<script>
// @ is an alias to /src
import {mapState} from 'vuex';
import {ref} from 'vue';

export default {
  name: 'HomeView',
    setup() {

        const country = ref('');
        const timer = ref(null);

        return {
            country,
            timer,
        }
    },
  methods: {
    changeValue(value) {
      clearTimeout(this.timer);
      this.timer = setTimeout(() => {
        this.$store.dispatch('getLeagues', this.country);
      }, 1000)
    },
    deleteTimer() {
        clearTimeout(this.timer);
    },
  },
  computed: {
    ...mapState(['leagues']),
  },
  mounted() {
    this.$store.dispatch('getLeagues')
  }
}
</script>

